@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap'); 

body{ 
  font-family: 'Lato', sans-serif; 
  background: linear-gradient(50deg, #34e7e4,#fff, #0fbcf9); 
  display: flex; 
  justify-content: center; 
  align-items: center; 

} 



*{ 
  margin: 0; 
  padding: 0;
}
#root{ 
  width: 100%;  
  height: 100vh; 
  display: flex;  
  flex-direction: column;
  justify-content: center; 
  align-items: center;  
} 
.h2{ 
  text-align: center;  
  font-size: 3rem; 
  margin-bottom: 30px;
}

@media screen and (max-width: 300px ) {
  body{  
    background:  #0fbcf9;
  }
}